.pagination {
	display: flex;

	margin-bottom: 50px;

	&__item {
		margin-right: 4px;


		text-align: center; }

	&__link {
		display: block;
		width: 26px;
		height: 26px;
		padding-top: 7px;
		color: white;
		background-color: $colorTheme;
		opacity: 0.3; }


	&__link.active {
		background-color: $colorTheme;
		opacity: 1; } }

