html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.icon-arrow-left {
  width: 1em;
  height: 1em;
  fill: ffffff;
}

.icon-arrow-right {
  width: 1em;
  height: 1em;
  fill: #fff;
}

.icon-burger {
  width: 1em;
  height: 1em;
  fill: #ccc;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-icon1 {
  width: 0.85em;
  height: 1em;
  fill: initial;
}

.icon-icon2 {
  width: 0.88em;
  height: 1em;
  fill: initial;
}

.icon-icon3 {
  width: 0.79em;
  height: 1em;
  fill: #fff;
}

.icon-link {
  width: 0.79em;
  height: 1em;
  fill: #fff;
}

.icon-quotes {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

body {
  font-family: "HelveticaNeue", sans-serif;
  font-size: 12px;
  line-height: 18px;
  background: url("../img/bg.jpg") repeat;
}

a {
  text-decoration: none;
}

a:hover {
  opacity: 0.7;
}

a:active {
  opacity: 0.4;
}

.container {
  width: 940px;
  margin: 0 auto;
}

.content__header {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.content__header-line {
  flex-grow: 1;
  min-height: 60px;
  background-color: #e8303c;
  opacity: 0.3;
}

.content__header-center {
  flex: none;
  margin: 0 10px;
  background-color: #e8303c;
}

.content__title {
  font-family: "Bebas", sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 400;
  padding: 14px 20px;
  color: #ffffff;
  text-transform: uppercase;
}

.main-nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
  margin: 0 10px;
}

.main-nav__toggler {
  display: none;
  position: absolute;
  top: 15px;
  right: 0;
  background-color: transparent;
}

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav__item {
  line-height: 20px;
  margin-left: 27px;
}

.nav__link {
  color: #b7b4b4;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.post-block {
  position: relative;
  width: 670px;
  padding-bottom: 40px;
  margin-bottom: 45px;
  background: url("../img/line.png") repeat-x;
  background-position: bottom left;
}

.post-block__icon {
  position: absolute;
  top: 0;
  left: -85px;
  width: 75px;
  height: 75px;
  font-size: 32px;
  text-align: center;
  padding-top: 20px;
  background-color: #e8303c;
}

.post-block__img {
  margin-bottom: 25px;
}

.post {
  position: relative;
  overflow: hidden;
}

.post p {
  margin-bottom: 20px;
}

.post__title {
  font-size: 18px;
  line-height: 18px;
  color: #e8303c;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.post__title_with-link {
  display: flex;
  flex-direction: column;
}

.post__quotes {
  padding: 20px 20px 20px 75px;
  font-style: italic;
  background: #f2f2f2;
}

.post__link {
  font-size: 12px;
  color: #bababa;
  text-transform: none;
  margin-top: 5px;
}

.options {
  float: left;
  width: 140px;
}

.options__name {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #e8303c;
  margin-bottom: 10px;
}

.options__defin {
  display: flex;
  margin-bottom: 15px;
}

.options__link {
  color: #bababa;
}

.options__link_after-line {
  position: relative;
  margin-left: 16px;
}

.options__link_after-line::after {
  position: absolute;
  top: 4px;
  left: -8px;
  content: "";
  width: 1px;
  height: 10px;
  background-color: #bababa;
}

.pagination {
  display: flex;
  margin-bottom: 50px;
}

.pagination__item {
  margin-right: 4px;
  text-align: center;
}

.pagination__link {
  display: block;
  width: 26px;
  height: 26px;
  padding-top: 7px;
  color: white;
  background-color: #e8303c;
  opacity: 0.3;
}

.pagination__link.active {
  background-color: #e8303c;
  opacity: 1;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 700px;
  }
  .post-block {
    width: 500px;
  }
  .post-block__img img {
    width: 500px;
  }
  .sidebar {
    width: 170px;
  }
  .ph-gallery__item {
    width: 70px;
  }
  .ph-gallery__wrapper {
    justify-content: space-around;
  }
}

@media only screen and (max-width: 768px) {
  body {
    font-size: 14px;
  }
  .container {
    width: 90%;
  }
  .content__header-line {
    display: none;
  }
  .main-nav {
    flex-direction: column;
  }
  .main-nav__toggler {
    display: block;
  }
  .logo {
    padding-top: 20px;
  }
  .nav {
    display: none;
    width: 100%;
    margin: 20px 0;
    border-top: 1px solid #dbdbdb;
  }
  .nav__item {
    margin: 0;
  }
  .nav__link {
    display: block;
    text-align: center;
    padding: 10px;
    border-bottom: 1px solid #dbdbdb;
  }
  .nav__item:last-child {
    border: none;
  }
  .nav.active {
    display: block;
  }
  .sidebar {
    display: none;
  }
  .post-block {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .post-block__img img {
    width: 100%;
  }
  .post-block__icon {
    display: none;
  }
  .post__title {
    text-align: center;
  }
  .left {
    float: none;
  }
  .options {
    display: flex;
    justify-content: space-between;
    width: 100%;
    order: 1;
  }
  .options__item {
    text-align: center;
  }
  .options__link {
    width: 100%;
  }
  .pagination {
    justify-content: center;
  }
}

.btn {
  display: none;
}

.icon-burger {
  font-size: 40px;
}

.post__icon-quotes .icon-quotes {
  position: absolute;
  top: 20px;
  left: 30px;
  font-size: 28px;
  fill: #e8303c;
}

.modal-content-close {
  display: none;
  position: fixed;
  top: 50px;
  right: 50px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  font-size: 0;
  border: 0;
  z-index: 25;
}

.active.modal-content-close {
  display: block;
}

.modal-content-close::before,
.modal-content-close::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 2px;
  width: 3px;
  height: 19px;
  background-color: #d0d0d0;
  border-radius: 1px;
}

.modal-content-close::before {
  transform: rotate(45deg);
}

.modal-content-close::after {
  transform: rotate(-45deg);
}

.slick-arrow {
  position: absolute;
  top: 50%;
  margin-top: -30px;
  font-size: 0;
  width: 40px;
  height: 60px;
  z-index: 1;
}

.slick-next {
  right: 20px;
  background: transparent url("../img/slick-arrows.png") -20px -20px no-repeat;
}

.slick-prev {
  left: 20px;
  background: transparent url("../img/slick-arrows.png") -20px -112px no-repeat;
}

.modal-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-overlay-show {
  display: block;
}

.modal-carousel {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 80%;
  height: 80%;
  margin: 10%;
}

.modal-carousel__item {
  height: 100%;
}

.modal-carousel__item img {
  width: 100%;
  height: auto;
}

.modal-carousel-show {
  display: block;
}

.sidebar {
  float: right;
  width: 220px;
}

.sidebar__item {
  margin-bottom: 35px;
}

.sidebar__title-wrapper {
  display: flex;
  width: 100%;
  margin-bottom: 15px;
}

.sidebar__h3 {
  font-size: 14px;
  line-height: 16px;
  color: #e8303c;
  margin-right: 15px;
  text-transform: uppercase;
  flex: none;
}

.sidebar__title-bg {
  flex-grow: 1;
  background: url("../img/line.png") repeat-x;
  background-position: bottom;
}

.active.about-jetro p {
  display: none;
  color: #464646;
}

.categories__list-link {
  color: #464646;
}

.active.categories .categories__list {
  display: none;
}

.active.ph-gallery .ph-gallery__wrapper {
  display: none;
}

.ph-gallery__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.ph-gallery__item {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  border: 1px solid #e8303c;
  background-color: #f5b19f;
  cursor: pointer;
  overflow: hidden;
}
