.btn {
	display: none; }

.icon-burger {
	font-size: 40px; }

.post__icon-quotes .icon-quotes {
	position: absolute;
	top: 20px;
	left: 30px;
	font-size: 28px;

	fill: $colorTheme; }

.modal-content-close {
	display: none;
	position: fixed;
	top: 50px;
	right: 50px;

	width: 22px;
	height: 22px;

	background-color: transparent;
	cursor: pointer;
	font-size: 0;
	border: 0;

	z-index: 25; }

.active.modal-content-close {
	display: block; }

.modal-content-close::before,
.modal-content-close::after {
	content: "";
	position: absolute;
	top: 0px;
	left: 2px;
	width: 3px;
	height: 19px;
	background-color: #d0d0d0;
	border-radius: 1px; }


.modal-content-close::before {
	transform: rotate(45deg); }


.modal-content-close::after {
	transform: rotate(-45deg); }
