.post-block {

	position: relative;
	width: 670px;
	padding-bottom: 40px;
	margin-bottom: 45px;
	background: url("../img/line.png")  repeat-x;
	background-position: bottom left;


	&__icon {
		position: absolute;
		top: 0;
		left: -85px;
		width: 75px;
		height: 75px;
		font-size: 32px;
		text-align: center;
		padding-top: 20px;
		background-color: $colorTheme; }

	&__img {
		margin-bottom: 25px; } }

.post {

	position: relative;
	overflow: hidden;

	p {
		margin-bottom: 20px; }

	&__title {
		font-size: 18px;
		line-height: 18px;
		color: $colorTheme;
		text-transform: uppercase;
		margin-bottom: 20px;

		&_with-link {
			display: flex;
			flex-direction: column; } }

	&__quotes {
		padding: 20px 20px 20px 75px;
		font-style: italic;
		background: #f2f2f2; }

	&__link {
		font-size: 12px;
		color: #bababa;
		text-transform: none;
		margin-top: 5px; } }

