@include r(1200) {

	.container {
		width: 700px; }

	.post-block {
		width: 500px;

		&__img img {
			width: 500px; } }

	.sidebar {
		width: 170px; }

	.ph-gallery {

		&__item {
			width: 70px; }

		&__wrapper {
			justify-content: space-around; } } }


@include r(768) {

	body {
		font-size: 14px; }

	.container {
		width: 90%; }

	.content {

		&__header-line {

			display: none; } }

	.main-nav {
		flex-direction: column;

		&__toggler {
			display: block; } }

	.logo {
		padding-top: 20px; }

	.nav {
		display: none;
		width: 100%;
		margin: 20px 0;
		border-top: 1px solid #dbdbdb;

		&__item {
			margin: 0; }

		&__link {
			display: block;
			text-align: center;
			padding: 10px;
			border-bottom: 1px solid #dbdbdb; }

		&__item:last-child {
			border: none; } }

	.nav.active {
		display: block; }

	.sidebar {
		display: none; }

	.post-block {
		display: flex;
		flex-direction: column;
		width: 100%;

		&__img img {
			width: 100%; }

		&__icon {
			display: none; } }

	.post {

		&__title {
			text-align: center; } }

	.left {
		float: none; }

	.options {
		display: flex;
		justify-content: space-between;
		width: 100%;
		order: 1;

		&__item {
			text-align: center; }

		&__link {
			width: 100%; } }

	.pagination {
		justify-content: center; } }
