.options {
	float: left;
	width: 140px;


	&__name {
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		color: $colorTheme;
		margin-bottom: 10px; }

	&__defin {
		display: flex;
		margin-bottom: 15px; }

	&__link {
		color: #bababa;

		&_after-line {
			position: relative;
			margin-left: 16px; }

		&_after-line::after {
			position: absolute;
			top: 4px;
			left: -8px;
			content: "";
			width: 1px;
			height: 10px;
			background-color: #bababa; } } }

