.main-nav {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	min-height: 80px;
	margin: 0 10px;

	&__toggler {
		display: none;
		position: absolute;
		top: 15px;
		right: 0;
		background-color: transparent; } }

.nav {
	display: flex;
	justify-content: space-around;
	align-items: center;

	&__item {
		line-height: 20px;
		margin-left: 27px; }

	&__link {
		color: #b7b4b4;
		font-size: 16px;
		line-height: 20px;
		text-transform: uppercase; } }


