.slick-arrow {
	position: absolute;
	top: 50%;
	margin-top: -30px;
	font-size: 0;

	width: 40px;
	height: 60px;
	z-index: 1; }

.slick-next {
	right: 20px;
	background: transparent url("../img/slick-arrows.png") -20px -20px no-repeat; }

.slick-prev {
	left: 20px;
	background: transparent url("../img/slick-arrows.png") -20px -112px no-repeat; }

