.content {

	&__header {
		display: flex;
		justify-content: center;
		margin-bottom: 25px; }

	&__header-line {
		flex-grow: 1;
		min-height: 60px;
		background-color: $colorTheme;
		opacity: 0.3; }

	&__header-center {
		flex: none;
		margin: 0 10px;

		background-color: $colorTheme; }

	&__title {
		font-family: "Bebas", sans-serif;
		font-size: 24px;
		line-height: 32px;
		font-weight: 400;
		padding: 14px 20px;
		color: #ffffff;
		text-transform: uppercase; } }

